"use client";

import React from "react";

import { useState, useEffect, useRef } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Send, Smile } from "lucide-react";
import { ChatMessage, fetchChats } from "@/lib/api";

export default function ChatPage() {
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [inputValue, setInputValue] = useState("");
  const [username, setUsername] = useState("");
  const [userID, setUserID] = useState("");
  const [nameInput, setNameInput] = useState("");
  const [showNamePrompt, setShowNamePrompt] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const [now, setNow] = useState(Date.now);

  const wsRef = useRef<WebSocket | null>(null);
  const webSocketBase = process.env.NEXT_PUBLIC_WEBSOCKET_BASE_URL;

  useEffect(() => {
    if (!username) return;

    const ws = new WebSocket(`${webSocketBase}/chat`);
    wsRef.current = ws;

    ws.onopen = () => {
      console.log("âœ… WebSocket connected");
    };

    ws.onmessage = (event) => {
      try {
        const msg = JSON.parse(event.data);

        setMessages((prev) => [
          ...prev,
          {
            id: msg.id ?? crypto.randomUUID(),
            userId: msg.userId,
            username: msg.username,
            text: msg.text,
            timestamp: msg.timestamp * 1000,
          },
        ]);
      } catch (err) {
        console.error("Invalid WS message", err);
      }
    };

    ws.onerror = (err) => {
      console.error("WebSocket error", err);
    };

    ws.onclose = () => {
      console.log("âŒ WebSocket closed");
    };

    return () => {
      ws.close();
      wsRef.current = null;
    };
  }, [username, userID, webSocketBase]);

  const USER_ID_KEY = "juzchat:userId";
  const USERNAME_KEY = "juzchat:username";

  const GetChats = async () => {
    try {
      const chats = await fetchChats();
      setMessages(chats);
    } catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    (async () => {
      await GetChats();
    })();
  }, []);

  const fetchName = async () => {
    let userId = localStorage.getItem(USER_ID_KEY);

    if (!userId) {
      userId = crypto.randomUUID();
      localStorage.setItem(USER_ID_KEY, userId);
    }

    setUserID(userId);

    const username = localStorage.getItem(USERNAME_KEY);

    if (!username) {
      setShowNamePrompt(true);
    } else {
      setUsername(username);
    }
  };

  useEffect(() => {
    (async () => {
      fetchName();
    })();
  }, []);

  const handleNameSubmit = () => {
    const trimmedName = nameInput.trim();
    if (!trimmedName) return;

    let userId = localStorage.getItem(USER_ID_KEY);

    if (!userId) {
      userId = crypto.randomUUID();
      localStorage.setItem(USER_ID_KEY, userId);
    }

    localStorage.setItem(USERNAME_KEY, trimmedName);

    setUsername(trimmedName);
    setShowNamePrompt(false);
  };

  const handleNameKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === "Enter") {
      e.preventDefault();
      handleNameSubmit();
    }
  };

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const handleSendMessage = () => {
    if (!inputValue.trim()) return;
    if (!wsRef.current || wsRef.current.readyState !== WebSocket.OPEN) return;

    const payload = {
      id: crypto.randomUUID(),
      userId: localStorage.getItem(USER_ID_KEY),
      username,
      text: inputValue,
      timestamp: Date.now(),
    };

    wsRef.current.send(JSON.stringify(payload));
    setInputValue("");
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  const formatTime = (date: Date) => {
    const now = new Date();
    const diffMs = now.getTime() - date.getTime();
    const diffMins = Math.floor(diffMs / 60000);

    if (diffMins < 1) return "now";
    if (diffMins < 60) return `${diffMins}m ago`;
    const diffHours = Math.floor(diffMins / 60);
    if (diffHours < 24) return `${diffHours}h ago`;
    return date.toLocaleDateString();
  };

  useEffect(() => {
    const interval = setInterval(() => {
      setNow(Date.now());
    }, 60 * 1000); // every 1 minute

    return () => clearInterval(interval);
  }, []);

  return (
    <div className="flex items-center justify-center min-h-screen bg-linear-to-br from-amber-50 via-amber-25 to-green-50 p-4">
      {/* Name Prompt Modal */}
      {showNamePrompt && (
        <div className="fixed inset-0 bg-black/20 backdrop-blur-sm flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-3xl shadow-2xl p-8 max-w-md w-full animate-fade-in">
            <div className="text-center space-y-6">
              <h2 className="text-2xl font-bold text-gray-800">
                Hi <span className="text-3xl">ðŸ˜Š</span> What should we call you?
              </h2>
              <Input
                value={nameInput}
                onChange={(e) => setNameInput(e.target.value)}
                onKeyDown={handleNameKeyPress}
                autoFocus
                placeholder="Your nameâ€¦"
                className="rounded-full border-amber-200 bg-amber-50 text-gray-800 placeholder:text-gray-400 text-center py-3 text-lg focus:border-amber-300 focus:bg-white transition-colors"
              />
              <Button
                onClick={handleNameSubmit}
                disabled={!nameInput.trim()}
                className="w-full rounded-full bg-linear-to-r from-amber-400 to-amber-500 hover:from-amber-500 hover:to-amber-600 text-white font-semibold py-3 shadow-md hover:shadow-lg transition-all disabled:opacity-50 disabled:cursor-not-allowed"
              >
                Start chatting â†’
              </Button>
              <p className="text-xs text-gray-500">
                No login. No account. Just chat.
              </p>
            </div>
          </div>
        </div>
      )}

      <div
        className="w-full max-w-2xl flex flex-col h-screen `md:h-150 bg-white rounded-3xl shadow-lg overflow-hidden"
        style={{ display: showNamePrompt ? "none" : "flex" }}
      >
        {/* Header */}
        <div className="bg-linear-to-r from-amber-50 to-green-50 px-6 py-6 border-b border-amber-100">
          <h1 className="text-3xl font-bold text-gray-800 text-balance">
            JuzChat
          </h1>
          <p className="text-sm text-gray-500 mt-1">Just come. Just chat.</p>
          <p className="text-xs text-gray-400 mt-3 flex items-center gap-1">
            <span className="inline-block w-2 h-2 bg-green-400 rounded-full animate-pulse"></span>
            Messages disappear after 3 hours
          </p>
        </div>

        {/* Messages Container */}
        <div className="flex-1 overflow-y-auto p-6 space-y-4 scrollbar-hide">
          {messages.map((message) => {
            const isMine = message.userId === userID;

            return (
              <div
                key={message.id}
                className={`flex flex-col gap-2 animate-fade-in ${
                  isMine ? "items-end" : "items-start"
                }`}
              >
                <div className="flex items-baseline gap-2">
                  {!isMine && (
                    <span className="font-semibold text-gray-700 text-sm">
                      {message.username}
                    </span>
                  )}
                  <span className="text-xs text-gray-400">
                    {formatTime(new Date(message.timestamp))}
                  </span>
                </div>

                <div
                  className={`rounded-2xl px-4 py-3 max-w-xs w-fit shadow-sm transition-shadow ${
                    isMine
                      ? "bg-linear-to-br from-green-100 to-green-200 text-gray-800"
                      : "bg-linear-to-br from-amber-50 to-amber-100 text-gray-800"
                  }`}
                >
                  {message.text}
                </div>
              </div>
            );
          })}

          <div ref={messagesEndRef} />
        </div>

        {/* Input Area */}
        <div className="bg-white border-t border-amber-100 p-6">
          <div className="flex items-center gap-3">
            <Input
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              onKeyPress={handleKeyPress}
              placeholder="Juz say somethingâ€¦ ðŸ˜Š"
              className="flex-1 rounded-full border-amber-200 bg-amber-50 text-gray-800 placeholder:text-gray-400 focus:border-amber-300 focus:bg-white transition-colors"
            />
            <Button
              onClick={handleSendMessage}
              disabled={!inputValue.trim()}
              className="rounded-full h-10 w-10 p-0 bg-linear-to-r from-amber-400 to-amber-500 hover:from-amber-500 hover:to-amber-600 text-white shadow-md hover:shadow-lg transition-all disabled:opacity-50 disabled:cursor-not-allowed"
              aria-label="Send message"
            >
              <Send className="h-5 w-5" />
            </Button>
            <button
              className="h-10 w-10 rounded-full bg-green-100 hover:bg-green-200 text-green-600 flex items-center justify-center transition-colors"
              aria-label="Add emoji"
            >
              <Smile className="h-5 w-5" />
            </button>
          </div>
          <p className="text-xs text-gray-400 mt-3 text-center">
            Chatting as{" "}
            <span className="font-semibold text-gray-600">{username}</span>
          </p>
        </div>
      </div>

      <style jsx>{`
        .scrollbar-hide {
          -ms-overflow-style: none;
          scrollbar-width: none;
        }
        .scrollbar-hide::-webkit-scrollbar {
          display: none;
        }
        @keyframes fadeIn {
          from {
            opacity: 0;
            transform: translateY(8px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
        .animate-fade-in {
          animation: fadeIn 0.3s ease-out;
        }
      `}</style>
    </div>
  );
}
